*{
    margin: 0px;
    padding: 0px;
}



.context {
    width: 100%;
    position: absolute;
    top:50vh;

}

.context h1{
    text-align: center;
    color: #fff;
    font-size: 50px;
}


.area{
    background: #4e54c8;
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);
    width: 100%;
    height:100vh;


}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255);
    animation: animate 25s linear infinite;
    bottom: -150px;

}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 22s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 40px;
    height: 40px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 150px;
    height: 150px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 16s;
}



@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}





/*
Wave
*/
#pageWrapper{
    position: fixed;
    width: 50%;
    height: 100%;
}
.wave,
.wave::before,
.wave::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 250vw;
    height: 250vw;
    margin-left: -125vw;
    transform-origin: 50% 50%;
    background-image: linear-gradient( rgba(150,255,255,0.25), rgba(166, 19, 171, 0.25) );
    border-radius: 38% 42%;
    box-shadow: inset 0 0 10vw rgba(255,255,255,0.25);
    animation: spin 64s infinite linear;
    z-index: -3;
}

.wave::before {
    width: 105%;
    height: 95%;
    margin-top: -125vw;
    transform-origin: 49% 51%;
    background-image:
            linear-gradient(rgba(255,180,150,0.25), #009DCD);
    border-radius: 40% 38%;
    animation: spin 52s infinite linear;
}

.wave::after {
    width: 102%;
    height: 98%;
    margin-top: -125vw;
    transform-origin: 51% 49%;
    background-image:
            linear-gradient(rgba(100,240,150,0.5),rgba(80,200,255,0.25));
    border-radius: 48% 42%;
    animation: spin 40s infinite linear;
}

@keyframes  spin {
    100% { transform: rotate(360deg); }
}

.wrapper {
    display: table;
    margin: 0 auto;
}

@-webkit-keyframes button-bounce {
    0% { transform: scale(1);
    }
    10% { transform: scale(0.96); }
    20% { transform: scale(1.04); }
    52%, 100% { transform: scale(1); }
}
@-moz-keyframes button-bounce {
    0% { transform: scale(1); }
    10% { transform: scale(0.96); }
    20% { transform: scale(1.04); }
    52%, 100% { transform: scale(1); }
}
@keyframes button-bounce {
    0% { transform: scale(1); }
    10% { transform: scale(0.96); }
    20% { transform: scale(1.04); }
    52%, 100% { transform: scale(1); }
}
@-webkit-keyframes button-blip {
    0% { transform: scaleX(1) scaleY(1); opacity: 0; }
    10% { transform: scaleX(1) scaleY(1); opacity: .7; }
    30%, 99% { transform: scaleX(1.08) scaleY(1.35); opacity: 0; }
    100% { transform: scaleX(1) scaleY(1); opacity: 0; }
}
@-moz-keyframes button-blip {
    0% { transform: scaleX(1) scaleY(1); opacity: 0; }
    10% { transform: scaleX(1) scaleY(1); opacity: .7; }
    30%, 99% { transform: scaleX(1.08) scaleY(1.35); opacity: 0; }
    100% { transform: scaleX(1) scaleY(1); opacity: 0; }
}
@keyframes button-blip {
    0% { transform: scaleX(1) scaleY(1); opacity: 0; }
    10% { transform: scaleX(1) scaleY(1); opacity: .7; }
    30%, 99% { transform: scaleX(1.08) scaleY(1.35); opacity: 0; }
    100% { transform: scaleX(1) scaleY(1); opacity: 0; }
}